


video::-webkit-media-controls {
    display: none;
}

.nimo__banner_group{


    .nimo__banner_group_link{
        
        


        img{
            width: 100%;
            height: 100vh;
            object-fit: cover;
        }
    }

    .nimo__banner_group_video{
        width: 1000px;
        margin: 0 auto;
        position: absolute;
        top: 11.5%;
        left: 11%;
        display: flex;
        justify-content: space-between;
        

        

        .nimo__banner_video{

            width: 850px;
            height: 468px;
            margin-top: 1.32rem;
            

            video{
                
                width: 100%;
                height: 100%;
                border-top-left-radius:5px ;
                border-top-right-radius: 5px;
                object-fit: cover;
                position: relative;
                z-index: 1;
                

            }
            .loadvideo{
                
                background:#777777;
                background-image:url(data:image/png;base64,);
                background-repeat: no-repeat;
                background-size: 200px;
                background-position: 50%;
                border-radius: 5px;
                left: 0;
                width: calc(100% - 150px);
                height: 88.8%;
                z-index: 0;
                position: absolute;
                top: 4%;
            }
        }
        .nimo__banner_video_controls{

            width: 100%;
            background-color: var(--background-controls);
            height:33px;
            margin-top: -.25rem;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius:5px ;
            color: white;
            display: flex;
            justify-content: space-between;
            padding: 0.15rem 1rem 0 1rem;;
            align-items: center;
            transition: all linear .2s;

            .controls__left{

                display: flex;
                align-items: center;

                .controls__start{

                    margin-right: 1rem;
                    cursor: pointer;

                    svg{

                        width: 15px;

                        path{
                            fill: white;
                        }
                    }
                }
                .controls__reset{

                    cursor: pointer;

                    svg{
                        width: 17px;

                        path{
                            fill: white;
                        }
                    }
                }
            }

            .controls__right{

                display: flex;

                .controls__sound{

                    display: flex;
                    align-items: center;

                    svg{
                        
                        margin-right: 1rem;
                        width: 17px;

                        path{
                            fill: white;
                        }
                    }

                    div{

                        margin-right: 1.2rem;
                        margin-top: -.7rem;
                        width: 120px;
                        position: relative;
                        

                        input{
                            -webkit-appearance: none;
                            width: 100%;
                            height: 2.5px;
                            background: #555555;
                            border-radius: 5px;

                            &::-webkit-slider-thumb {
                                -webkit-appearance: none;
                                appearance: none;
                                width: 13px;
                                height:13px;
                                background: white;
                                cursor: pointer;
                                border-radius: 100%;
                                z-index: 10;
                                position: relative;
                              }
                        }

                        .bar{

                            position: absolute;
                            height: 2.5px;
                            bottom: 17%;
                            left: 0;
                            background-color: white;
                            z-index: 1;

                        }   
                        
                    }
                }
                .controls__resolution{

                    margin-right: 1.5rem;
                    background-color: #333333;
                    padding: 0 1.2rem;
                    border-radius: 25px;
                    height: 19px;
                    margin-top: 1.2px;

                    span{

                        font-size: 12px;
                        margin-top: 1px;
                        display: block;
                    }
                }
                .controls__link{

                    background-color: rgb(255, 192, 0);
                    padding: 0 1rem;
                    height: 19px;
                    padding: 0 1.2rem;
                    border-radius: 25px;
                    height: 19px;
                    margin-top: 1.2px;
                    cursor: pointer;

                    a{
                        text-decoration: none;
                        font-size: 12px;
                        margin-top: 1px;
                        display: block;
                        color: white;
                    }
                }
            }
        }
        .nimo__banner_video_list{

            list-style-type:none ;
            margin-top: 1.05rem;
            padding: 0;
         

            .nimo__banner_video_item{

                padding: 4px 0;
                cursor: pointer;

                
                
                .nimo__banner_video_content{

                    position: relative;

                    &.hover > .nimo__banner_video_text{
                        display: block;
                        
                    }
                    &.hover > .overlay{
                        opacity: 0;
                    }
                    &.hover{
                        
                        border: 2px solid white;
                        margin: -2px;
                        border-radius: 5px;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        position: relative;

                        &::after{

                            content: "";
                            position: absolute;
                            left: -10%;
                            top: 41%;
                            border: 6px solid red;
                            border-color: transparent white transparent  transparent;

                        }

                    }
                    &:hover > .nimo__banner_video_text{
                        display: block;
                        
                    }
                    &:hover > .overlay{
                        opacity: 0;
                    }
                    &:hover{

                        border: 2px solid white;
                        margin: -2px;
                        border-radius: 5px;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        position: relative;

                        &::after{

                            content: "";
                            position: absolute;
                            left: -10%;
                            top: 41%;
                            border: 6px solid red;
                            border-color: transparent white transparent  transparent;

                        }
                    }

                    .nimo__banner_video_img{

                        width: 136px;
                        height: 77px;
                        

                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                            border-radius:5px ;
                        }
                    }

                    .nimo__banner_video_text{

                        position: absolute;
                        background-color: rgba($color: #000000, $alpha: .7);
                        width: 100%;
                        top: 72%;
                        border-bottom-left-radius:5px ;
                        border-bottom-right-radius:5px ;
                        overflow: hidden;
                        height: 21.5px;
                        line-clamp: 1;
                        text-overflow: ellipsis;
                        white-space: nowrap; 
                        color: white;
                        display: none;
                        background: black;

                        span{
                            font-size: 13px;
                            padding: 0 .2rem;
                            color: white;
                        }

                        
                    }

                    .overlay{
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        background-color: #000000;
                        opacity: .4;
                    }
                }
                
            }
        }
    }
}

